  box-sizing: border-box;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  background: #f5f5f5;
  min-height: 100vh;
  background: url(../../public/assets/images/areabg.webp) repeat-y;
  width: 960px;
  margin: 0 auto;
}

