t-weight: bold;\n}\n.status[data-v-42c7142d] {\n  padding: 2px 6px;\n  border-radius: 2px;\n  font-weight: bold;\n}\n.status.pending[data-v-42c7142d] {\n  background: #faad14;\n  color: white;\n}\n.status.completed[data-v-42c7142d] {\n  background: #52c41a;\n  color: white;\n}\n.timestamp[data-v-42c7142d] {\n  color: #999;\n}\n.response-data[data-v-42c7142d] {\n  margin-top: 8px;\n  padding-top: 8px;\n  border-top: 1px solid #f0f0f0;\n}\n.response-content[data-v-42c7142d] {\n  margin-top: 8px;\n  background: #f8f8f8;\n  border-radius: 4px;\n  padding: 8px;\n  max-height: 300px;\n  overflow-y: auto;\n}\n.response-content pre[data-v-42c7142d] {\n  margin: 0;\n  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;\n  font-size: 11px;\n  line-height: 1.4;\n  color: #333;\n  white-space: pre-wrap;\n  word-break: break-word;\n}\n", "",{"version":3,"sources":["webpack://./src/chrome-extensions/DevToolsPanel.vue"],"names":[],"mappings":";AAijBA;EACA,sBAAA;EACA,aAAA;EACA,gFAAA;EACA,mBAAA;EACA,iBAAA;EACA,4DAAA;EACA,YAAA;EACA,cAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,mBAAA;EACA,oBAAA;EACA,gCAAA;AACA;AAEA;EACA,SAAA;EACA,WAAA;EACA,eAAA;AACA;AAEA;EACA,aAAA;EACA,mBAAA;EACA,SAAA;AACA;AAEA;EACA,kBAAA;EACA,WAAA;EACA,eAAA;AACA;AAEA;EACA,kBAAA;EACA,kBAAA;EACA,WAAA;EACA,iBAAA;EACA,kBAAA;EACA,uBAAA;AACA;AAEA;EACA,iBAAA;EACA,kBAAA;EACA,yBAAA;EACA,gBAAA;AACA;AAEA;EACA,gCAAA;EACA,kBAAA;EACA,iCAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;EACA,8BAAA;AACA;AAEA;EACA,kBAAA;AACA;AAEA;EACA,wDAAA;EACA,eAAA;EACA,WAAA;EACA,qBAAA;EACA,kBAAA;AACA;AAEA;EACA,aAAA;EACA,SAAA;EACA,eAAA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;AACA;AAEA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;AACA;AAEA;EACA,WAAA;AACA;AAEA;EACA,eAAA;EACA,gBAAA;EACA,6BAAA;AACA;AAEA;EACA,eAAA;EACA,mBAAA;EACA,kBAAA;EACA,YAAA;EACA,iBAAA;EACA,gBAAA;AACA;AAEA;EACA,SAAA;EACA,wDAAA;EACA,eAAA;EACA,gBAAA;EACA,WAAA;EACA,qBAAA;EACA,sBAAA;AACA","sourcesContent":["<template>\n  <div class=\"devtools-panel\">\n    <div class=\"panel-header\">\n      <h3></h3>\n      <div class=\"connection-status\">\n        <el-tag :type=\"devtoolsConnected ? 'success' : 'warning'\" size=\"mini\">\n          {{ connectionStatus }}\n        </el-tag>\n        <el-button @click=\"prevPage\" size=\"mini\"></el-button>\n        <el-button @click=\"nextPage\" size=\"mini\"></el-button>\n        <el-button @click=\"getPageInfo\" size=\"mini\" type=\"info\"></el-button>\n        <el-button @click=\"reconnectDevTools\" size=\"mini\" type=\"warning\" v-if=\"!devtoolsConnected\"></el-button>\n        <el-button @click=\"clearData\" size=\"mini\" type=\"danger\"></el-button>\n      </div>\n    </div>\n    <div class=\"data-section\">\n      <el-empty v-if=\"recommendData.length === 0\" class=\"empty-state\" description=\"\"></el-empty>\n      <div v-else class=\"request-list\">\n        <div v-for=\"item in recommendData\" :key=\"item.requestId\" class=\"request-item\"\n          :class=\"{ 'completed': item.status === 'completed' }\">\n          <div class=\"request-info\">\n            <div class=\"request-meta\">\n              <span class=\"status\" :class=\"item.status\">{{ item.status }}</span>\n              <span class=\"timestamp\">{{ formatTime(item.timestamp) }}</span>\n            </div>\n          </div>\n          <div v-if=\"item.responseData\" class=\"response-data\">\n            <el-row class=\"roles\" type=\"flex\">\n              <span v-for=\"role in parseListData(item.responseData)?.equip_list\" :key=\"role.eid\">\n                <RoleImage :key=\"role.eid\" :other_info=\"role.other_info\" :roleInfo=\"parserRoleData(role)\" />\n              </span>\n            </el-row>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n<script>\nimport RoleImage from '@/components/RoleInfo/RoleImage.vue'\nexport default {\n  name: 'DevToolsPanel',\n  data() {\n    return {\n      recommendData: [],\n      expandedItems: [],\n      processedRequests: new Set(), // ID\n      devtoolsConnected: false, // DevTools Protocol\n      connectionStatus: '...', // \n      connectionCheckTimer: null // \n    }\n  },\n  components: {\n    RoleImage\n  },\n  computed: {\n\n  },\n  mounted() {\n    this.initMessageListener()\n    this.checkConnectionStatus()\n\n    // // 5\n    // this.connectionCheckTimer = setInterval(() => {\n    //   this.checkConnectionStatus()\n    // }, 5000)\n  },\n  beforeDestroy() {\n    // Chrome\n    this.removeMessageListener()\n    // \n    if (this.connectionCheckTimer) {\n      clearInterval(this.connectionCheckTimer)\n      this.connectionCheckTimer = null\n    }\n    // \n    this.recommendData = []\n    this.expandedItems = []\n  },\n  methods: {\n    nextPage(){\n      // ChromeAPI\n      this.clickPageButton('next')\n    },\n    \n    prevPage(){\n      // ChromeAPI\n      this.clickPageButton('prev')\n    },\n    \n    getPageInfo(){\n      // \n      this.getPagerInfo()\n    },\n    \n    reconnectDevTools(){\n      // DevTools\n      this.connectionStatus = '...'\n      this.checkConnectionStatus()\n      this.$message.info('DevTools...')\n    },\n    \n    async clickPageButton(direction) {\n      try {\n        // \n        const [activeTab] = await chrome.tabs.query({ active: true, currentWindow: true })\n        \n        if (!activeTab || !activeTab.url.includes('cbg.163.com')) {\n          this.$message.warning('')\n          return\n        }\n\n        // ChromeAPI\n        if (!this.devtoolsConnected) {\n          this.$message.warning('DevTools')\n          return\n        }\n\n        // ChromeAPIJavaScript\n        const result = await chrome.debugger.sendCommand(\n          { tabId: activeTab.id },\n          'Runtime.evaluate',\n          {\n            expression: `\n              (function() {\n                try {\n                  // idpagerdiv\n                  const pagerDiv = document.getElementById('pager')\n                  if (!pagerDiv) {\n                    return 'ERROR:'\n                  }\n                  \n                  let targetButton = null\n                  const isNext = '${direction}' === 'next'\n                  \n                  if (isNext) {\n                    //  - HTML\n                    // 1. \"\"\n                    const allLinks = pagerDiv.querySelectorAll('a')\n                    for (let link of allLinks) {\n                      const text = link.textContent.trim()\n                      if (text === '') {\n                        targetButton = link\n                        break\n                      }\n                    }\n                    \n                    // 2. \"\"goto\n                    if (!targetButton) {\n                      for (let link of allLinks) {\n                        const href = link.getAttribute('href')\n                        const text = link.textContent.trim()\n                        // goto\n                        if (href && href.includes('goto(') && !link.classList.contains('on')) {\n                          // \n                          const currentPageLink = pagerDiv.querySelector('a.on')\n                          if (currentPageLink) {\n                            const currentPageText = currentPageLink.textContent.trim()\n                            const currentPage = parseInt(currentPageText)\n                            const linkPage = parseInt(text)\n                            // \n                            if (!isNaN(linkPage) && linkPage > currentPage) {\n                              targetButton = link\n                              break\n                            }\n                          }\n                        }\n                      }\n                    }\n                  } else {\n                    // \n                    const allLinks = pagerDiv.querySelectorAll('a')\n                    \n                    // 1. \"\"\n                    for (let link of allLinks) {\n                      const text = link.textContent.trim()\n                      if (text === '') {\n                        targetButton = link\n                        break\n                      }\n                    }\n                    \n                    // 2. \"\"goto\n                    if (!targetButton) {\n                      for (let link of allLinks) {\n                        const href = link.getAttribute('href')\n                        const text = link.textContent.trim()\n                        // goto\n                        if (href && href.includes('goto(') && !link.classList.contains('on')) {\n                          // \n                          const currentPageLink = pagerDiv.querySelector('a.on')\n                          if (currentPageLink) {\n                            const currentPageText = currentPageLink.textContent.trim()\n                            const currentPage = parseInt(currentPageText)\n                            const linkPage = parseInt(text)\n                            // \n                            if (!isNaN(linkPage) && linkPage < currentPage) {\n                              targetButton = link\n                              break\n                            }\n                          }\n                        }\n                      }\n                    }\n                  }\n                  \n                  if (!targetButton) {\n                    return 'ERROR:${direction === 'next' ? '' : ''}'\n                  }\n                  \n                  // \n                  if (targetButton.disabled || targetButton.classList.contains('disabled')) {\n                    return 'ERROR:${direction === 'next' ? '' : ''}${direction === 'next' ? '' : ''}'\n                  }\n                  \n                  // \n                  const currentPageLink = pagerDiv.querySelector('a.on')\n                  let currentPageInfo = ''\n                  if (currentPageLink) {\n                    const currentPageText = currentPageLink.textContent.trim()\n                    currentPageInfo = ' (' + currentPageText + ')'\n                  }\n                  \n                  // \n                  targetButton.click()\n                  return 'SUCCESS:${direction === 'next' ? '' : ''}' + currentPageInfo\n                } catch (error) {\n                  return 'ERROR: - ' + error.message\n                }\n              })()\n            `\n          }\n        )\n\n        // ChromeAPI\n          if (result && result.result && result.result.value) {\n            const message = result.result.value\n            \n            if (message.startsWith('SUCCESS:')) {\n              this.$message.success(message.substring(8)) // \"SUCCESS:\"\n              console.log(`${direction === 'next' ? '' : ''}`)\n            } else if (message.startsWith('ERROR:')) {\n              this.$message.warning(message.substring(6)) // \"ERROR:\"\n              console.warn(`${direction === 'next' ? '' : ''}:`, message)\n            } else {\n              this.$message.error('')\n              console.error(':', result)\n            }\n          } else {\n            this.$message.error('')\n            console.error(':', result)\n          }\n          \n        } catch (error) {\n          console.error(`${direction === 'next' ? '' : ''}:`, error)\n          \n          // \n          if (error.message && error.message.includes('Could not establish connection')) {\n            this.devtoolsConnected = false\n            this.connectionStatus = ''\n            this.$message.error('DevTools')\n          } else {\n            this.$message.error(': ' + error.message)\n          }\n        }\n    },\n    \n    async getPagerInfo() {\n      try {\n        // \n        const [activeTab] = await chrome.tabs.query({ active: true, currentWindow: true })\n        \n        if (!activeTab || !activeTab.url.includes('cbg.163.com')) {\n          this.$message.warning('')\n          return\n        }\n\n        // ChromeAPI\n        if (!this.devtoolsConnected) {\n          this.$message.warning('DevTools')\n          return\n        }\n\n        // ChromeAPIJavaScript\n        const result = await chrome.debugger.sendCommand(\n          { tabId: activeTab.id },\n          'Runtime.evaluate',\n          {\n            expression: `\n              (function() {\n                try {\n                  // idpagerdiv\n                  const pagerDiv = document.getElementById('pager')\n                  if (!pagerDiv) {\n                    return 'ERROR:'\n                  }\n                  \n                  // \n                  const currentPageLink = pagerDiv.querySelector('a.on')\n                  let currentPage = ''\n                  if (currentPageLink) {\n                    currentPage = currentPageLink.textContent.trim()\n                  }\n                  \n                  // \n                  const allPageLinks = pagerDiv.querySelectorAll('a[href*=\"goto(\"]')\n                  const pageNumbers = []\n                  allPageLinks.forEach(link => {\n                    const text = link.textContent.trim()\n                    if (text.match(/^\\d+$/)) {\n                      pageNumbers.push(parseInt(text))\n                    }\n                  })\n                  \n                  // \n                  const totalPages = pageNumbers.length > 0 ? Math.max(...pageNumbers) : ''\n                  \n                  // /\n                  const hasPrev = pagerDiv.querySelector('a[href*=\"goto(\"]') && \n                                 pagerDiv.textContent.includes('')\n                  const hasNext = pagerDiv.querySelector('a[href*=\"goto(\"]') && \n                                 pagerDiv.textContent.includes('')\n                  \n                  return 'SUCCESS:' + currentPage + '' + totalPages + ' (:' + (hasPrev ? '' : '') + ', :' + (hasNext ? '' : '') + ')'\n                } catch (error) {\n                  return 'ERROR: - ' + error.message\n                }\n              })()\n            `\n          }\n        )\n\n        // \n        if (result && result.result && result.result.value) {\n          const message = result.result.value\n          \n          if (message.startsWith('SUCCESS:')) {\n            this.$message.info(message.substring(8)) // \"SUCCESS:\"\n            console.log(':', message)\n          } else if (message.startsWith('ERROR:')) {\n            this.$message.warning(message.substring(6)) // \"ERROR:\"\n            console.warn(':', message)\n          } else {\n            this.$message.error('')\n            console.error(':', result)\n          }\n        } else {\n          this.$message.error('')\n          console.error(':', result)\n        }\n        \n      } catch (error) {\n        console.error(':', error)\n        \n        // \n        if (error.message && error.message.includes('Could not establish connection')) {\n          this.devtoolsConnected = false\n          this.connectionStatus = ''\n          this.$message.error('DevTools')\n        } else {\n          this.$message.error(': ' + error.message)\n        }\n      }\n    },\n    parserRoleData(data) {\n      const roleInfo = new window.RoleInfoParser(data.large_equip_desc, { equip_level: data.equip_level })\n      return roleInfo.result\n      // return {\n      //   RoleInfoParser: roleInfo,\n      //   roleInfo: roleInfo.result,\n      //   accept_bargain: data.accept_bargain,\n      //   collect_num: data.collect_num,\n      //   dynamic_tags: data.dynamic_tags,\n      //   eid: data.eid,\n      //   highlight: data.highlight,\n      //   is_split_independent_role: data.is_split_independent_role,\n      //   is_split_main_role: data.is_split_main_role,\n      //   large_equip_desc: data.large_equip_desc,\n      //   level: data.level,\n      //   other_info: data.other_info,\n      //   school: data.school,\n      //   seller_nickname: data.seller_nickname,\n      //   server_name: data.server_name,\n      //   serverid: data.serverid,\n      //   price: data.price,\n      //   sum_exp: data.sum_exp,\n      //   create_time: data.create_time,\n      //   update_time: data.create_time,\n      //   all_equip_json: '',\n      //   all_summon_json: '',\n      //   split_price_desc: '',\n      //   pet_price: '',\n      //   equip_price: '',\n      //   base_price: '',\n      //   history_price: '',\n      // }\n    },\n    parseListData(responseDataStr) {\n      //  Request.JSONP.request_map.request_(xxxx) xxxx\n      const match = responseDataStr.match(/Request\\.JSONP\\.request_map\\.request_\\d+\\((.*)\\)/)\n      let templateJSONStr = '{}'\n      if (match) {\n        templateJSONStr = match[1]\n      }\n      try {\n        const templateJSON = JSON.parse(templateJSONStr)\n        return templateJSON\n      } catch (error) {\n        console.error(':', error)\n        return {}\n      }\n    },\n    initMessageListener() {\n      console.log('DevToolsPanel mounted, initializing listener')\n\n      // \n      if (typeof chrome !== 'undefined' && chrome.runtime) {\n        // \n        if (window.cbgDevToolsListener) {\n          chrome.runtime.onMessage.removeListener(window.cbgDevToolsListener)\n        }\n\n        // \n        window.cbgDevToolsListener = (request, sender, sendResponse) => {\n          console.log('DevToolsPanel received Chrome message:', request.action)\n          this.handleChromeMessage(request, sender, sendResponse)\n          sendResponse({ success: true })\n        }\n\n        // \n        chrome.runtime.onMessage.addListener(window.cbgDevToolsListener)\n        console.log('Chrome message listener registered for DevToolsPanel')\n      }\n    },\n\n    removeMessageListener() {\n      // Chrome\n      if (typeof chrome !== 'undefined' && chrome.runtime && window.cbgDevToolsListener) {\n        chrome.runtime.onMessage.removeListener(window.cbgDevToolsListener)\n        delete window.cbgDevToolsListener\n        console.log('Chrome message listener removed for DevToolsPanel')\n      }\n    },\n\n    checkConnectionStatus() {\n      // Chrome\n      if (typeof chrome !== 'undefined' && chrome.runtime) {\n        // ping\n        chrome.runtime.sendMessage({ action: 'ping' }, (response) => {\n          if (chrome.runtime.lastError) {\n            console.log('Chrome extension connection check failed:', chrome.runtime.lastError)\n            this.devtoolsConnected = false\n            this.connectionStatus = ''\n          } else if (response && response.success) {\n            console.log('Chrome extension connection check successful:', response)\n            this.devtoolsConnected = true\n            this.connectionStatus = ''\n          } else {\n            console.log('Chrome extension connection check failed: invalid response')\n            this.devtoolsConnected = false\n            this.connectionStatus = ''\n          }\n        })\n      } else {\n        console.log('Chrome runtime not available')\n        this.devtoolsConnected = false\n        this.connectionStatus = 'Chrome'\n      }\n    },\n\n    handleChromeMessage(request, sender, sendResponse) {\n      switch (request.action) {\n        case 'updateRecommendData':\n          this.recommendData = request.data || []\n\n          // \n          if (this.recommendData && this.recommendData.length > 0) {\n            this.recommendData.forEach(item => {\n              if (item.status === 'completed' &&\n                item.responseData &&\n                item.url &&\n                item.requestId &&\n                !this.processedRequests.has(item.requestId)) {\n\n                // \n                this.processedRequests.add(item.requestId)\n                console.log(`: ${item.requestId}`)\n\n                // \n                this.$api.spider.parseResponse({\n                  url: item.url,\n                  response_text: item.responseData\n                }).then(res => {\n                  console.log(` ${item.requestId} :`, res)\n                  if (res.code === 200) {\n                    console.log(` ${item.requestId} :`, res.data)\n                  } else {\n                    console.error(` ${item.requestId} :`, res.message)\n                  }\n                }).catch(error => {\n                  console.error(` ${item.requestId} :`, error)\n                  // \n                  this.processedRequests.delete(item.requestId)\n                })\n              }\n            })\n          }\n          break\n\n        case 'devtoolsConnected':\n          this.devtoolsConnected = true\n          this.connectionStatus = ''\n          this.$message.success(request.message)\n          break\n\n        case 'showDebuggerWarning':\n          this.devtoolsConnected = false\n          this.connectionStatus = ''\n          this.$message.warning(request.message)\n          break\n\n        case 'clearRecommendData':\n          this.recommendData = []\n          this.expandedItems = []\n          this.processedRequests.clear()\n          console.log('')\n          break\n      }\n    },\n\n\n    clearData() {\n      this.recommendData = []\n      this.expandedItems = []\n      // background script\n      if (typeof chrome !== 'undefined' && chrome.runtime) {\n        chrome.runtime.sendMessage({\n          action: 'clearRecommendData'\n        })\n      }\n    },\n\n    toggleResponse(index) {\n      const expandedIndex = this.expandedItems.indexOf(index)\n      if (expandedIndex > -1) {\n        this.expandedItems.splice(expandedIndex, 1)\n      } else {\n        this.expandedItems.push(index)\n      }\n    },\n\n    formatTime(timestamp) {\n      if (!timestamp) return ''\n      const date = new Date(timestamp)\n      return date.toLocaleTimeString()\n    }\n  }\n}\n</script>\n\n<style scoped>\n.devtools-panel {\n  box-sizing: border-box;\n  padding: 16px;\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;\n  background: #f5f5f5;\n  min-height: 100vh;\n  background: url(../../public/assets/images/areabg.webp) repeat-y;\n  width: 960px;\n  margin: 0 auto;\n}\n\n.panel-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 20px;\n  padding-bottom: 12px;\n  border-bottom: 1px solid #e0e0e0;\n}\n\n.panel-header h3 {\n  margin: 0;\n  color: #333;\n  font-size: 18px;\n}\n\n.connection-status {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n}\n\n.data-section h4 {\n  margin: 0 0 12px 0;\n  color: #666;\n  font-size: 14px;\n}\n\n.empty-state {\n  text-align: center;\n  padding: 40px 20px;\n  color: #999;\n  background: white;\n  border-radius: 4px;\n  border: 1px dashed #ddd;\n}\n\n.request-list {\n  background: white;\n  border-radius: 4px;\n  border: 1px solid #e0e0e0;\n  overflow: hidden;\n}\n\n.request-item {\n  border-bottom: 1px solid #f0f0f0;\n  padding: 12px 16px;\n  transition: background-color 0.2s;\n}\n\n.request-item:last-child {\n  border-bottom: none;\n}\n\n.request-item:hover {\n  background-color: #fafafa;\n}\n\n.request-item.completed {\n  background-color: #f0f9ff;\n  border-left: 3px solid #1890ff;\n}\n\n.request-info {\n  margin-bottom: 8px;\n}\n\n.request-url {\n  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;\n  font-size: 12px;\n  color: #333;\n  word-break: break-all;\n  margin-bottom: 4px;\n}\n\n.request-meta {\n  display: flex;\n  gap: 12px;\n  font-size: 11px;\n}\n\n.method {\n  background: #1890ff;\n  color: white;\n  padding: 2px 6px;\n  border-radius: 2px;\n  font-weight: bold;\n}\n\n.status {\n  padding: 2px 6px;\n  border-radius: 2px;\n  font-weight: bold;\n}\n\n.status.pending {\n  background: #faad14;\n  color: white;\n}\n\n.status.completed {\n  background: #52c41a;\n  color: white;\n}\n\n.timestamp {\n  color: #999;\n}\n\n.response-data {\n  margin-top: 8px;\n  padding-top: 8px;\n  border-top: 1p